<template>
    <div>
        <h1>你好</h1>
        <button @click="btn">照片</button>
        <button @click="btn1">开始录制</button>
        <button @click="btn2">停止录制</button>

    </div>
</template>

<script>
import axios from 'axios';
export default {
    data(){
        return{

        }
    },
    async created(){
        let data = await axios.get('http://www.tsml520.cn/wx/sdk')
        data = data.data
        wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: 'wx3df629936bf31f75', // 必填，公众号的唯一标识
            timestamp: data.timestamp, // 必填，生成签名的时间戳
            nonceStr: data.noncestr, // 必填，生成签名的随机串
            signature: data.signature,// 必填，签名
            jsApiList: ['chooseImage','startRecord','stopRecord','pauseVoice'] // 必填，需要使用的JS接口列表
        });
        console.log('后端获取的数据',data)
        await this.init(data)

        let noncestr = data.noncestr; 
        let signature = data.signature;
        let timestamp = data.timestamp
    },
    methods:{
        btn1(){
            wx.startRecord();
        },
        btn2(){
            wx.stopRecord({
            success: function (res) {
                console.log('停止录制。。。',res)
                var localId = res.localId;
            }
            });
        },
        btn(){
            wx.chooseImage({
                count: 1, // 默认9
                sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
                sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
                success: function (res) {
                    var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
                    }
                });
        },
        init(data){
            console.log('data',data)

            wx.ready(function(){
                // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
                wx.checkJsApi({
                    jsApiList: ['chooseImage','startRecord','stopRecord','pauseVoice'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
                    success: function(res) {
                        console.log('res',res)
                    // 以键值对的形式返回，可用的api值true，不可用为false
                    // 如：{"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
                    }
                });
            });
            wx.error(function(res){
                console.log('err',res)
                // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
            });
        },
    
    }   

}
</script>

<style>

</style>



